import { useContext, useState } from 'react';
import { Button, Container, Table, ListGroup } from 'react-bootstrap';
import Headline from '../components/IU/Headline';
import { Context } from '../index';


const ArchThirdPage = () => {
    const { archivariusResult } = useContext(Context);
    const [pathResultShow, setPathResultShow] = useState(false);
    const { optionTitle, wordCount, createdAt } = archivariusResult.eventDataResult;
    const createDateA = createdAt.split('T')[0];
    const createDateB = createdAt.split('T')[1].split('.')[0];
    const date = `${createDateA} ${createDateB}`;
    const wordResult = archivariusResult.wordResult.sort((a, b) => b.quantity - a.quantity);
    const pathResult = archivariusResult.pathResult;
    console.log('pathResult', pathResult);

    return <Container>
        <Headline>  3000 (3/3)</Headline>
        <p style={{ marginTop: "10px", marginBottom: '10px' }}> : {optionTitle}</p>
        <p style={{ marginTop: "10px", marginBottom: '10px' }}>  : {wordCount} .</p>
        <p style={{ marginTop: "10px", marginBottom: '10px' }}>: {date}</p>
        {/* <div>
            <Button style={{ marginTop: "10px" }}>    </Button>
        </div>
        <div>
            <Button style={{ marginTop: "20px" }}>  </Button>
        </div>
        <div>
            <Button style={{ marginTop: "20px", marginBottom: '20px' }}>   </Button>
        </div> */}
        <Table bordered hover>
            <thead>
                <tr style={{textAlign: "center"}}>
                    <th> ..</th>
                    <th> </th>
                    <th>  </th>
                </tr>
            </thead>
            <tbody>
                {wordResult.map((item, index) => {
                    return <tr key={index} style={{textAlign: "center"}}>
                        <td>{index + 1}</td>
                        <td>{item.word}</td>
                        <td>{item.quantity}</td>
                    </tr>
                })}
            </tbody>
        </Table>
        <div>
            <Button onClick={() => setPathResultShow(!pathResultShow)} style={{ marginTop: "20px", marginBottom: '30px' }}>
                {pathResultShow ? " " : " "}
            </Button>
        </div>
        {pathResultShow && <Table  bordered hover >
            <thead style={{textAlign: "center"}}>
                <tr>
                    <th> ..</th>
                    <th> </th>
                    <th> </th>
                </tr>
            </thead>
            <tbody>
                {pathResult.map((item, index) => {
                    return <tr key={index}>
                        <td  style={{textAlign: "center"}}>{index + 1}</td>
                        <td>{item.fullPath}</td>
                        <td  style={{textAlign: "center"}}>{item.word}</td>
                    </tr>
                })}
            </tbody>
        </Table>}
    </Container>
}

export default ArchThirdPage;